body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html{line-height:1.15;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%}body{margin:0}article,aside,footer,header,nav,section{display:block}h1{font-size:2em;margin:.67em 0}figcaption,figure,main{display:block}figure{margin:1em 40px}hr{box-sizing:content-box;height:0;overflow:visible}pre{font-family:monospace,monospace;font-size:1em}a{background-color:transparent;-webkit-text-decoration-skip:objects}abbr[title]{border-bottom:none;text-decoration:underline;-webkit-text-decoration:underline dotted;text-decoration:underline dotted}b,strong{font-weight:inherit}b,strong{font-weight:bolder}code,kbd,samp{font-family:monospace,monospace;font-size:1em}dfn{font-style:italic}mark{background-color:#ff0;color:#000}small{font-size:80%}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline}sub{bottom:-.25em}sup{top:-.5em}audio,video{display:inline-block}audio:not([controls]){display:none;height:0}img{border-style:none}svg:not(:root){overflow:hidden}button,input,optgroup,select,textarea{font-family:sans-serif;font-size:100%;line-height:1.15;margin:0}button,input{overflow:visible}button,select{text-transform:none}button,html [type=button],[type=reset],[type=submit]{-webkit-appearance:button}button::-moz-focus-inner,[type=button]::-moz-focus-inner,[type=reset]::-moz-focus-inner,[type=submit]::-moz-focus-inner{border-style:none;padding:0}button:-moz-focusring,[type=button]:-moz-focusring,[type=reset]:-moz-focusring,[type=submit]:-moz-focusring{outline:1px dotted ButtonText}fieldset{padding:.35em .75em .625em}legend{box-sizing:border-box;color:inherit;display:table;max-width:100%;padding:0;white-space:normal}progress{display:inline-block;vertical-align:baseline}textarea{overflow:auto}[type=checkbox],[type=radio]{box-sizing:border-box;padding:0}[type=number]::-webkit-inner-spin-button,[type=number]::-webkit-outer-spin-button{height:auto}[type=search]{-webkit-appearance:textfield;outline-offset:-2px}[type=search]::-webkit-search-cancel-button,[type=search]::-webkit-search-decoration{-webkit-appearance:none}::-webkit-file-upload-button{-webkit-appearance:button;font:inherit}details,menu{display:block}summary{display:list-item}canvas{display:inline-block}template{display:none}[hidden]{display:none}.clearfix:after{visibility:hidden;display:block;font-size:0;content:'';clear:both;height:0}html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}.srOnly{position:absolute;width:1px;height:1px;margin:-1px;border:0;padding:0;white-space:nowrap;-webkit-clip-path:inset(100%);clip-path:inset(100%);clip:rect(0 0 0 0);overflow:hidden}*{padding:0;margin:0}html{font-size:62.5%}.wrapper{max-width:1280px;width:95%;margin:0 auto}ul{list-style:none}a{text-decoration:none;color:inherited}img{width:100%;height:auto;display:block}header,.headerOverlay{min-height:35vh}header{background-image:url(/amanda-monteiro-project-5/static/media/header.bd238ec6.jpg);background-size:cover;background-position:center;margin-bottom:55px}.headerOverlay{background-color:rgba(241,250,238,0.5);width:100%;padding:20px}h1{margin-top:0;padding:25px 0;font-family:"Kaushan Script",cursive;font-size:7rem;letter-spacing:2px;text-shadow:3px 1px rgba(0,0,0,0.2)}h2{font-size:4rem;font-weight:400;letter-spacing:1.5px;font-style:italic}html{background-color:#f1faee;background-image:url(/amanda-monteiro-project-5/static/media/flowers.222d56c2.png)}body{color:#1d3557;font-size:1.8rem;font-style:"Poppins",sans-serif;letter-spacing:0.9px}.content{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:25px;gap:25px}h3{font-family:"Kaushan Script",cursive;font-size:3.1rem;letter-spacing:2px;text-shadow:1px 1px rgba(0,0,0,0.2);text-align:center;margin-bottom:25px}.taskContainer{padding:25px 80px;background-color:#f1faee;border:3px solid rgba(29,53,87,0.8);border-radius:10px;box-shadow:3px 1px rgba(0,0,0,0.2)}ul{display:block;margin:0 auto}li,.taskUpdate{display:flex;align-items:center}span{display:inline-block;padding:0 10px}button{border-style:none;color:white;padding:5px;cursor:pointer;letter-spacing:0.9px}button:focus,.taskUpdate:focus,a:focus{outline:1px dashed #1d3557}input[type=checkbox]{opacity:0;transform:scale(3.2);position:absolute}.taskUpdate{margin:10px 25px 10px 0}.taskUpdate:hover,.removeBtn:hover{transform:scale(1.1)}.noBtn,.yesBtn{padding:5px;border-radius:5px;color:white}.noBtn:focus,.yesBtn:focus{outline:none}.noBtn{background-color:#457b9d}.yesBtn{background-color:#a8dadc}.removeBtn{background-color:rgba(230,57,70,0.6);color:#f1faee;border-radius:5px;height:3rem}.removeBtn:hover{background-color:#e63946}.yes{color:rgba(29,53,87,0.3);text-decoration:line-through;background-color:transparent}input[type=text]{margin:35px 0 25px 0;caret-color:#e63946;padding:5px;border-style:none;background-color:transparent;border-bottom:1px solid #1d3557;border-radius:5px 0 0 0;width:70%}input[type=text]:focus{background-color:rgba(168,218,220,0.3);outline:none}.addBtn{background-color:transparent;border-bottom:3px solid rgba(29,53,87,0.9);border-right:5px solid rgba(29,53,87,0.9);border-radius:5px 5px 5px 0;color:#1d3557;width:30%}.addBtn:hover{background-color:#1d3557;color:#f1faee}.suggestBtn{display:block;margin:25px auto;background-color:rgba(168,218,220,0.6);border-radius:5px;padding:15px;color:#1d3557}.suggestBtn:hover{background-color:#457b9d;color:white}footer{background-color:rgba(29,53,87,0.8);color:#f1faee;padding:20px 0;text-align:center;margin-top:55px}a:visited{color:inherit}a:hover{border-bottom:2px solid #e63946}@media (max-width: 940px){.taskContainer{padding:20px 30px}.addBtn{width:40%}input[type=text]{width:60%}}@media (max-width: 768px){.content{grid-template-columns:repeat(1, 1fr)}}@media (max-width: 480px){h1{font-size:6rem}h2{font-size:3rem}.taskContainer{padding:5px 10px}}

